@import '../../../../vars';

.row {
  width: 100%;
  display: flex;
  align-items: center;
  font-size: 18px;
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }

  &__title {
    min-width: 200px;
    margin-right: 20px;
    font-weight: 500;
    color: var(--color10);
  }

  &__value {
    color: var(--color10);
  }
}

@media (max-width: 400px) {
  .row {
    justify-content: space-between;

    &__title {
      min-width: auto;
      max-width: 200px;
    }
  }
}
