@import '../../../vars';

.user {
  display: flex;
  padding: 20px 0;
  border-bottom: 1px solid var(--color22);

  &__inner {
    display: block;
  }

  &__avatarLink {
    margin-right: 30px;
  }

  &__link {
    font-size: 20px;
    transition: all 0.2s;

    &:hover {
      transform: scale(1.1);
    }
  }

  &__avatar {
    width: 100px;
    height: 100px;
    border-radius: 50%;
  }

  &__FLName {
    font-weight: 500;
    color: var(--color28);
  }

  &__role {
    margin-left: 10px;
    font-size: 16px;
  }

  &__nickname {
    width: fit-content;
    color: var(--color29);
  }

  &__info {
    display: flex;
    flex-direction: column;
    // justify-content: space-between;

    a {
      margin-bottom: 14px;
    }
  }

  &__bio {
    font-size: 18px;
    color: var(--color10);
  }
}

.banned {
  text-decoration: line-through;
}

@media (max-width: 500px) {
  .user {
    &__bio {
      display: none;
    }
  }
}
