.taskInfo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 85%;
  font-size: 22px;
  transition: all 0.2s;
  cursor: pointer;

  &:hover {
    opacity: 0.7;
  }

  &__icon {
    margin-top: 4px;
    margin-right: 10px;
  }

  &__title {
    line-height: 22px;
    transition: all 0.2s;

    &:hover {
      opacity: 0.7;
    }
  }

  &__title_completed {
    text-decoration: line-through;
  }
}
